export default {
	data() {
		return {
			seasonList:'',
			cookList:"",
			TabCur: '',
			page:1,
			lazyLoad:true,
			urlV:''+new Date().getTime()
		}
	},	
	onLoad(){
		//这是对季节的请求
		this.getSeasonList()
	},
	methods:{
		getSeasonList(){
			this.$request({
				url:"/front/food/querySeasonList"
			}).then(res=>{
				if(res.data.code==0){
					this.seasonList=res.data.data;
					this.TabCur=res.data.data[0].id
					this.getCookList(this.TabCur)
				}
			})
		},
		//这是做季节下面的列表请求
		getCookList(id){
			this.TabCur=id
			this.$request({
				url:"/front/food/queryCookList",
				data:{
					page:this.page,
					limit:10,
					seasonId:id
				}
			}).then(res => {
				if(res.data.code==0){
					this.cookList=res.data.data
				}
			})
		},

		toIndexDetail(id){
			uni.navigateTo({
				url:`/pages/indexDetail/indexDetail?id=${id}`
			})
		}
	}
	
}
